@tailwind base;
@tailwind components;
@tailwind utilities;

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

/* Global Styles */

:root {
  --base-font-size: 1rem;           /* 1rem = 16px by default in most browsers */
  --header-height: 4rem;            /* 64px */
  --primary-color: #1a1a1a;
  --secondary-color: #f39c12;
  --background-color: #f8f9fa;
  --text-color: #333;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: Arial, sans-serif;
  font-size: var(--base-font-size);
}

body {
  background-color: var(--background-color);
  color: var(--text-color);
  line-height: 1.6;
}

/* Navbar (Collapsible) */
.navbar {
  display: flex;
  justify-content: space-between;
  align-items: center;
  background: var(--primary-color);
  padding: 1rem 1.25rem;
  color: white;
  position: sticky;
  top: 0;
  z-index: 1000;
}

.navbar .nav-links {
  display: flex;
  gap: 1.25rem;
  transition: max-height 0.3s ease;
}

.navbar.collapsed .nav-links {
  max-height: 0;
  overflow: hidden;
}

.navbar a {
  text-decoration: none;
  color: white;
  font-size: 1.25rem;
  transition: color 0.3s ease;
}

.navbar a:hover {
  color: var(--secondary-color);
}

/* Hero Section */
.hero {
  text-align: center;
  padding: 6.25rem 1.25rem;
  background: url('https://example.com/hero-image.jpg') no-repeat center center/cover;
  color: white;
}

.hero h2 {
  font-size: 2.25rem;
  margin-bottom: 0.5rem;
}

.hero p {
  font-size: 1.25rem;
}

/* Sections */
section {
  padding: 3.75rem 1.25rem;
  text-align: center;
}

/* Services List (Example for infinite scroll container) */
.infinite-scroll-container {
  overflow-y: auto;
  max-height: 50vh;  /* Adjust as needed */
  padding: 1.25rem;
}

/* Contact Links */
.contact a {
  color: var(--secondary-color);
  text-decoration: none;
  font-weight: bold;
  font-size: 1.25rem;
}

/* Footer */
.footer {
  background: #111;
  color: white;
  text-align: center;
  padding: 1.25rem;
  margin-top: 1.25rem;
  font-size: 1.25rem;
}

/* Responsive Design */
@media (max-width: 48rem) {
  .navbar .nav-links {
    flex-direction: column;
    text-align: center;
  }
  .hero h2 {
    font-size: 1.75rem;
  }
  .hero p {
    font-size: 1rem;
  }
}